@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Fjalla+One&subset=latin-ext);
.App {
  text-align: center; }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  font-family: "Poppins", sans-serif;
  line-height: 1.6;
  background-color: #fff; }

@media screen and (max-width: 1023px) {
  .mobile {
    display: none; } }

@media (min-width: 1024px) {
  .desktop {
    display: none; } }

.navigation {
  font-size: 1.7rem;
  background: transparent;
  margin: .0 4rem 0 2rem;
  display: flex;
  justify-content: space-between; }

.nav-logo {
  display: flex;
  align-items: center;
  justify-content: flex-start; }

.nav-logo-item {
  height: 4rem;
  background: transparent;
  text-decoration: none;
  color: #B9BCA2;
  font-size: 2rem;
  padding: 0 .5rem;
  border-radius: 100%;
  border: 3px solid #B9BCA2; }

.social-media {
  display: flex;
  align-items: center;
  font-size: 2.5rem; }

.main-menu {
  display: flex;
  align-items: center; }

.navbar-item {
  text-decoration: none;
  padding: 1.5rem 1.5rem;
  color: #ece8df; }
  .navbar-item:visited {
    color: #ece8df; }
  .navbar-item:hover {
    color: rgba(48, 49, 51, 0.9); }

.blinking {
  -webkit-animation: blinkingText 1s infinite;
          animation: blinkingText 1s infinite; }

@-webkit-keyframes blinkingText {
  0% {
    color: orange; }
  49% {
    color: transparent; }
  50% {
    color: transparent; }
  80% {
    color: transparent; }
  100% {
    color: orange; } }

@keyframes blinkingText {
  0% {
    color: orange; }
  49% {
    color: transparent; }
  50% {
    color: transparent; }
  80% {
    color: transparent; }
  100% {
    color: orange; } }

.active {
  color: rgba(48, 49, 51, 0.9) !important; }

.home {
  background: linear-gradient(to right bottom, rgba(47, 47, 48, 0.5), rgba(82, 129, 96, 0.3)), url("https://i.imgur.com/oWOj3ag.jpg") center/cover;
  height: 100vh;
  z-index: -1111;
  margin-top: -6rem;
  padding-top: 8rem;
  font-size: 3rem; }
  @media screen and (max-width: 768px) {
    .home {
      margin-top: -12rem; } }

.typewriter {
  max-width: 40%;
  white-space: pre-wrap;
  text-align: left;
  padding-left: 3rem;
  color: #B9BCA2;
  margin-top: 20rem; }

.ime-i-prezime {
  color: #b4975a; }

.gallery {
  background: linear-gradient(to right bottom, rgba(100, 243, 140, 0.4), rgba(247, 157, 0, 0.6));
  height: 100%;
  z-index: -1111;
  margin-top: -6rem;
  padding-top: 8rem;
  font-size: 3rem; }
  @media screen and (max-width: 768px) {
    .gallery {
      margin-top: -12rem; } }

