$orange:rgb(255,165,0);
$orange-1:#b4975a;
$gray:rgba(48,49,51,.9);
//$white:rgb(236, 232, 223);
$white:#B9BCA2;
@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,700');
@import url('https://fonts.googleapis.com/css?family=Fjalla+One&subset=latin-ext');
*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}
html{
    box-sizing: border-box;
    font-size: 62.5%; //1rem = 10px , 16px/10px = 62.5%
}

body{
    
    
   font-family: "Poppins", sans-serif;
   // color: $color-font-primary;
    line-height: 1.6;
    background-color: #fff;
    
}



// to hide content if screeen size is smaller than 1024px, anywhere is invoked
@media screen and(max-width: 1023px) {
	.mobile {
		display: none;
	}
}
//to hide content if screen size is higher than 1024px;
@media (min-width: 1024px) {
	.desktop {
		display: none;
	}
}